.singlePost{
    flex:9;
}
.singlePostWrapper{
    padding:20px;
    padding-right: 0;
}
.singlePostImg{
    width: 100%;
    height:300px;
    border-radius: 5px;
    object-fit: cover;
}
.singlePostTitle{
    text-align: center;
    margin: 10px;
    font-family: serif;
    font-size: 28px;

}
.singlePostEdit{
    float:right;
    font-style: 16px;
}
.singlePostIcon{
    margin-left: 15px;
    cursor: pointer;
}
.singlePostIcon:first-child{
    color:teal;
}
.singlePostIcon:last-child{
    color:tomato;
}
.singlePostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color:#b39656;
    font-family:'Varela Round',sans-serif;

}

.singlePostDescription{
    color:#666;
    font-style: 18px;
line-height: 28px;
}
.singlePostDescription::first-letter{
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;
}