.sidebar{
    flex:3;
    margin:20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    margin-top: 80px;
    

}
.sidebarItem{
    display:flex;
    flex-direction: column;
}
.postImage{
    width: 200px;
    height:200px;
    align-items: center;
    margin-left: 65px;
    border-radius: 50%;
}
.sidebarTitle{
    margin:10px;
    padding:5px;
    width:80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela Round', sans-serif;
    font-size: 12px;
    color:#222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}

.sidebarItem > img {
    margin-top: 15px;
}
.sidebarItem > p {
    padding:30px;
}
.sidebarList{
    list-style-type: none;
    margin-bottom:30px;  
}
.sidebarListItem{
    display: inline-block;
    width:50%;
    margin-top: 15px;
 cursor: pointer;
}
sidebarSocial{
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sidebarIcons{
    font-size: 16px;
    margin-left: 15px;
    cursor: pointer;
}
